html, body, #root, #root > div {
    height: 100%;
}

body {
    background-color: #27272c;
}

h1 {
    font-family: 'Poppins', helvetica, arial, sans-serif;
    color: #ffffff;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-weight: normal;
}

h2 {
    font-family: "Andale Mono";
    margin: 0;
    font-size: 20px;
    color: #d70190;
    font-weight: lighter;
}


h3 {
    font-family: 'Poppins', helvetica, arial, sans-serif;
    margin: 10px 0;
    color: #6366ff;
    text-transform: uppercase;
    font-weight: lighter;
}

h4 {
    font-family: 'Poppins', helvetica, arial, sans-serif;
    font-weight: lighter;
    text-transform: uppercase;
    color: #fff41f;
    font-size: 15px;
    margin: 0 0 10px;
}

h5 {
    font-family: 'Poppins', helvetica, arial, sans-serif;
    font-weight: lighter;
    color: #00cdff;
    font-size: 15px;
    margin: 0 0 8px;
}

p {
    font-family: "Source Code Pro", sans-serif;
    color: #000000;
    margin: 0 0 5px;
}

a {
    color: #d70190;
    text-decoration: none;
}

a:hover {
    color: #6366ff;
}


.app-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

.app-div {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.app-title {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}

.bottom-div {
    display: -webkit-flex;
    display: flex;
    -webkit-align-content: center;
            align-content: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin-top: 20px;
}

/* counter buttons */
.counter-div {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    margin: 0 10px 40px;
}

.correct-counter {
    display: -webkit-flex;
    display: flex;
    -webkit-align-content: center;
            align-content: center;
    -webkit-justify-content: center;
            justify-content: center;
    padding: 5px;
    border-radius: 30px;
    background-color: aquamarine;
    margin-right: 5px;
    width: 100px;
    box-shadow: 0 3px 0 #00bc7a, 0 3px 3px rgba(0,0,0,.4), inset 0 1px 0 rgba(255,255,255,.3);
}

.incorrect-counter {
    display: -webkit-flex;
    display: flex;
    -webkit-align-content: center;
            align-content: center;
    -webkit-justify-content: center;
            justify-content: center;
    padding: 5px;
    border-radius: 30px;
    background-color: #ff9999;
    margin-left: 5px;
    width: 100px;
    box-shadow: 0 3px 0 #9d5a5a, 0 3px 3px rgba(0,0,0,.4), inset 0 1px 0 rgba(255,255,255,.3);
}

/* quiz */
.triad-name {
    margin-left: 20px;
}

.quiz-text {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    margin-bottom: 20px;
}

.quiz-results {
    width: 250px;
    height: auto;
}

.quiz-results p {
    color: white;
    letter-spacing: 1px;
    font-family: "Source Code Pro", sans-serif;
}

.results-row {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

/* keyboard */
.keyboard {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    box-shadow: 1px 5px 10px #000000;
    margin: 0 0 10px;
}

.white-key {
    padding: 10px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    -webkit-align-items: center;
            align-items: center;
    color: black;
    background-color: white;
    border: 1px solid lightgray;
    height: 220px;
    width: 60px;
    margin: 0 -5px 0 0;
}

.black-key {
    padding: 10px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    -webkit-align-items: center;
            align-items: center;
    color: white;
    background-color: black;
    z-index: 10;
    margin: 0 0 0 -10px;
    width: 45px;
    height: 150px;
    border: 0;
    border-top: 1px solid lightgray;
}

.pressed-key {
    background-color: #e0dede;
}

.black-key + .white-key {
    margin-left: -32px;
}

.white-key, .black-key {
    font-size: 12px;
    font-family: "Andale Mono";
    letter-spacing: 1px;
}

.white-key:hover, .black-key:hover {
    background-color: #e0dede;
    cursor: pointer;
}
